@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400|Roboto);
html{display:table;height:100%;width:100%}body{font-family:'Open Sans', sans-serif;vertical-align:middle;display:table-cell;padding:0;margin:0;background:#90CAF9;background-image:linear-gradient(45deg, #90CAF9, #E3F2FD);background-attachment:fixed}#root{text-align:center}.container{background-color:#fff;display:inline-block;margin:0.625rem 0;text-align:left;min-width:17rem;box-shadow:0 24px 38px 3px rgba(0,0,0,0.14),0 9px 46px 8px rgba(0,0,0,0.12),0 11px 15px -7px rgba(0,0,0,0.2)}.container h1{font-family:'Open Sans', sans-serif;font-weight:300;text-align:center;padding:1.5rem;margin:0}.container a{color:#000}.container a:active,.container a:hover{color:#616161}.container p{text-align:center;padding:0.625rem}.container ul{text-align:center;margin:0 0 1rem 0;padding:0}.container ul li{list-style-type:none;padding:0;margin:0}.container ul li a{display:block;padding:10px 40px 10px 40px;text-decoration:none;color:#000;transition:background 250ms ease}.container ul li a:active,.container ul li a:hover{background:#f5f5f5}.container__nav{display:inline-block;padding:10px 10px 10px 40px;margin:10px 0;text-decoration:none;color:#9E9E9E;transition:background 250ms ease}.container__nav:active,.container__nav:hover{background:#f5f5f5}

